@import "../../../../styles/vars";
@import "../../../../styles/mixins";


.webDevelopmentProcessBox {
  padding-top: 57px;
  @include flexCol();

  @include lgScreenMedia {
    padding-top: 50px;
    padding-left: 25px;
    padding-right: 25px;
    height: 100%;
  }

  @include smScreenMedia {
    padding-top: 25px;
  }

  &__container {
    background: #1C1C1C;
    border: 1px solid $color-light-blue;
    border-radius: 20px;
    padding: 0 28px 33px;
    max-width: 436px;
    width: 100%;
    height: 100%;

    @include lgScreenMedia {
      padding: 0 16px 20px;
      max-width: 575px;
    }

  }

  &__iconBlock {
    width: 128px;
    height: 128px;
    background: #1C1C1C;
    border-radius: 50%;
    border: 1px solid $color-light-blue;
    @include flexCol();
    margin-top: -57px;
    margin-bottom: 40px;
    margin-left: auto;
    margin-right: auto;

    @include lgScreenMedia {
      width: 100px;
      height: 100px;
      margin-top: -50px;
      margin-bottom: 20px;
    }

    @include smScreenMedia {
      width: 50px;
      height: 50px;
      margin-top: -25px;
    }
  }

  &__icon {
    @include svg(64px, $color-light-blue);

    @include lgScreenMedia {
      width: 40px;
      height: 40px;
    }

    @include smScreenMedia {
      width: 20px;
      height: 20px;
    }
  }

  &__title {
    color: $color-light-blue;
    @include setFont(26px, 500);
    text-align: center;
    margin-bottom: 20px;

    @include xxlScreenMedia {
      font-size: 26px;
    }

    @include xlScreenMedia {
      font-size: 24px;
    }

    @include lgScreenMedia {
      font-size: 20px;
    }
  }

  &__contentText {
    color: $color-white;
    overflow: hidden;
    transition: $transition;
  }

}
