@import "../../../styles/vars";
@import "../../../styles/mixins";

.webDevelopmentProcess {
  @include setDistance(padding-bottom,70);

  &__title {
    @include setDistance(margin-bottom,60);
    text-align: center;
  }

  &__mainContent {
    background-image: url("../../../assets/images/WebDev/webDevelopmentProcess/webDevProcessBg.png");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__deskWrapper {
    @include flexCol(space-between,stretch);
    gap: 30px;

    @include lgScreenMedia {
      display: none;
    }
  }

  &__slider {
    @include lgScreenMedia {
      margin-bottom: 28px;
    }

    @media (min-width: $lg-screen) {
      display: none!important;
    }

    :global(.slick-list) {
      display: flex;
    }

    :global(.slick-track) {
      display: flex!important;
      align-items: stretch;
    }
    :global(.slick-slide) {
      float: none!important;
      height: 100%;
    }

    :global(.slick-slide>div) {
      height: 100%;
    }
  }

}
